'use client';

import React, { useEffect, useState } from 'react';
import {
  Box,
  Typography,
  Container,
  CircularProgress,
  Grid,
  Card,
  CardContent,
  CardActions,
  IconButton,
  Tooltip,
  Dialog,
  DialogTitle,
  DialogContent,
  DialogActions,
  Button,
  useMediaQuery,
  useTheme,
} from '@mui/material';

import TrainIcon from '@mui/icons-material/Train';
import InfoIcon from '@mui/icons-material/Info';
import EditIcon from '@mui/icons-material/Edit';
import DeleteIcon from '@mui/icons-material/Delete';
import RefreshIcon from '@mui/icons-material/Refresh';
import AddIcon from '@mui/icons-material/Add';

interface Train {
  _id: string;
  name: string;
  number: string;
  source: string;
  destination: string;
  departureTime: string;
  arrivalTime: string;
  status: string;
  createdAt: string;
}

export default function TrainPage() {
  const [trains, setTrains] = useState<Train[]>([]);
  const [loading, setLoading] = useState(true);
  const [selectedTrain, setSelectedTrain] = useState<Train | null>(null);
  const [error, setError] = useState<string | null>(null);

  const theme = useTheme();
  const isMobile = useMediaQuery(theme.breakpoints.down('sm'));

  const fetchTrains = async () => {
    try {
      setLoading(true);
      setError(null);
      const response = await fetch('/api/trains'); // ✅ Replace with actual API route
      const data = await response.json();

      if (!Array.isArray(data)) {
        throw new Error('Invalid data received. Expected an array.');
      }

      setTrains(data);
    } catch (err: any) {
      console.error('Error fetching trains:', err);
      setError(err.message || 'Error fetching trains.');
    } finally {
      setLoading(false);
    }
  };

  useEffect(() => {
    fetchTrains();
  }, []);

  const handleView = (train: Train) => {
    setSelectedTrain(train);
  };

  const handleCloseDialog = () => {
    setSelectedTrain(null);
  };

  return (
    <Container maxWidth="xl" sx={{ py: 4 }}>
      <Box display="flex" justifyContent="space-between" alignItems="center" mb={4}>
        <Typography variant="h4" fontWeight="bold" display="flex" alignItems="center" gap={1}>
          <TrainIcon fontSize="large" />
          Train Management
        </Typography>

        <Box display="flex" gap={1}>
          <Tooltip title="Add New Train">
            <IconButton color="primary">
              <AddIcon />
            </IconButton>
          </Tooltip>

          <Tooltip title="Refresh">
            <IconButton color="secondary" onClick={fetchTrains}>
              <RefreshIcon />
            </IconButton>
          </Tooltip>
        </Box>
      </Box>

      {loading ? (
        <Box display="flex" justifyContent="center" alignItems="center" minHeight="40vh">
          <CircularProgress size={50} />
        </Box>
      ) : error ? (
        <Typography color="error" align="center">
          {error}
        </Typography>
      ) : trains.length === 0 ? (
        <Typography align="center" variant="h6">
          No trains available.
        </Typography>
      ) : (
        <Grid container spacing={3}>
          {trains.map((train) => (
            <Grid item xs={12} sm={6} md={4} lg={3} key={train._id}>
              <Card
                elevation={3}
                sx={{
                  borderRadius: 4,
                  transition: '0.3s',
                  '&:hover': { boxShadow: 10 },
                  background: '#fefefe',
                }}
              >
                <CardContent>
                  <Typography variant="h6" fontWeight="bold" gutterBottom>
                    {train.name} ({train.number})
                  </Typography>
                  <Typography variant="body2" gutterBottom>
                    <strong>Route:</strong> {train.source} ➡️ {train.destination}
                  </Typography>
                  <Typography variant="body2">
                    <strong>Departure:</strong> {train.departureTime}
                  </Typography>
                  <Typography variant="body2">
                    <strong>Arrival:</strong> {train.arrivalTime}
                  </Typography>
                  <Typography variant="body2">
                    <strong>Status:</strong>{' '}
                    <span
                      style={{
                        color:
                          train.status === 'active'
                            ? 'green'
                            : train.status === 'delayed'
                            ? 'orange'
                            : 'red',
                      }}
                    >
                      {train.status}
                    </span>
                  </Typography>
                </CardContent>

                <CardActions>
                  <Tooltip title="View Details">
                    <IconButton onClick={() => handleView(train)}>
                      <InfoIcon />
                    </IconButton>
                  </Tooltip>
                  <Tooltip title="Edit Train">
                    <IconButton>
                      <EditIcon />
                    </IconButton>
                  </Tooltip>
                  <Tooltip title="Delete Train">
                    <IconButton color="error">
                      <DeleteIcon />
                    </IconButton>
                  </Tooltip>
                </CardActions>
              </Card>
            </Grid>
          ))}
        </Grid>
      )}

      <Dialog open={!!selectedTrain} onClose={handleCloseDialog} fullWidth maxWidth="sm">
        <DialogTitle>Train Details</DialogTitle>
        <DialogContent dividers>
          {selectedTrain && (
            <>
              <Typography variant="body1" gutterBottom>
                <strong>Name:</strong> {selectedTrain.name}
              </Typography>
              <Typography variant="body1" gutterBottom>
                <strong>Number:</strong> {selectedTrain.number}
              </Typography>
              <Typography variant="body1" gutterBottom>
                <strong>Route:</strong> {selectedTrain.source} ➡️ {selectedTrain.destination}
              </Typography>
              <Typography variant="body1" gutterBottom>
                <strong>Departure:</strong> {selectedTrain.departureTime}
              </Typography>
              <Typography variant="body1" gutterBottom>
                <strong>Arrival:</strong> {selectedTrain.arrivalTime}
              </Typography>
              <Typography variant="body1" gutterBottom>
                <strong>Status:</strong> {selectedTrain.status}
              </Typography>
              <Typography variant="body1">
                <strong>Created At:</strong>{' '}
                {new Date(selectedTrain.createdAt).toLocaleString()}
              </Typography>
            </>
          )}
        </DialogContent>
        <DialogActions>
          <Button onClick={handleCloseDialog} color="primary">
            Close
          </Button>
        </DialogActions>
      </Dialog>
    </Container>
  );
}
